





















































import {Component, Vue} from 'vue-property-decorator';
import {Button, Field, Tab, Tabs, Uploader, Loading, Toast} from 'vant';
import 'vant/lib/toast/style';
import 'vant/lib/tab/style';
import 'vant/lib/tabs/style';
import 'vant/lib/field/style';
import 'vant/lib/button/style';
import 'vant/lib/loading/style';
import 'vant/lib/uploader/style';
import Prepare from '@/views/cert_to_chain/cert_scan_sign.vue';
import PDFJS from 'pdfjs-dist';
import {notes, NoteType} from '@/util/note';
import {CertGia, CertOwn} from '../../model/certs_model';
import {ToastUtil} from "../../util/toast";
import {generateUUID} from "../../util/utils";

Vue.use(Tab).use(Tabs).use(Field).use(Button).use(Uploader).use(Loading).use(Toast);

@Component({
    components: {Prepare, Tab, Tabs, Field, Button},
})
export default class InputCert extends Vue {
    private active = 0;
    private loadingPdf = false;
    private certOwn = new CertOwn();
    private giaKey = 'gia';
    private giaValue = '';
    private caratKey = 'carat';
    private caratValue = '';
    private colorKey = 'color';
    private colorValue = '';
    private clarityKey = 'clarity';
    private clarityValue = '';
    private cutKey = 'cut';
    private cutValue = '';

    public created(): void {
        this.$store.state.title = this.$t('cert.cert_upload');
    }

    public afterRead(file: any): void {
        this.loadingPdf = true;
        const reader = new FileReader();
        reader.onload = (file) => {
            const u = reader.result as string;
            if (u) {
                this.loadingPdf = true;
                this.readPdf(u).then((resultString) => {
                    // const reg6params = new RegExp(/GIA Report Number[ .]+(\w+)Shape and Cutting Style.+Carat Weight[ .]+ ([0-9.]+)\s*caratColor Grade[ .]+(\w+)Clarity Grade[ .]+(\w+)Cut Grade[ .]+(\w+)Polish/).exec(resultString);
                    /*
                    解析规则说明：cutGrade值情况有：excellent (EX)、very good (VG)、fair (F)、poor (P)
                       已测试过的有：excellent（一个单词类），very good（两个单词类）
                     */
                    const cert = new CertOwn();
                    const certGia = new CertGia();
                    let clarityGrade: RegExpExecArray | null;
                    let cutGrade = new RegExp(/Cut Grade[ .]+(\w+)Polish/).exec(resultString); //一个单词
                    {
                        if (cutGrade === null || cutGrade.toString() === "") {
                            cutGrade = new RegExp(/Cut Grade[ .]+(\w+)\s*(\w+)Polish/).exec(resultString);//两个单词
                            if (cutGrade !== null) {
                                console.log("cutGrade[1]======>" + cutGrade[1]);
                                certGia.cut = cutGrade[1] + " Good";///特别说明：两个单词的情况只有very good。硬编码写入
                                //解析规则说明： 有Cut Grade的话，Clarity Grade的值以Clarity Grade结尾
                                clarityGrade = new RegExp(/Clarity Grade[ .]+(\w+)Cut Grade/).exec(resultString);
                            } else {
                                //解析规则说明： 没有Cut Grade的话，Clarity Grade的值以Polish结尾
                                clarityGrade = new RegExp(/Clarity Grade[ .]+(\w+)Polish/).exec(resultString);
                            }
                        } else {
                            certGia.cut = cutGrade[1];
                            //解析规则说明： 有Cut Grade的话，Clarity Grade的值以Clarity Grade结尾
                            clarityGrade = new RegExp(/Clarity Grade[ .]+(\w+)Cut Grade/).exec(resultString);
                        }
                        if (clarityGrade !== null && clarityGrade[1] && clarityGrade[1] !== "") {
                            certGia.clarity = clarityGrade[1].toString();
                        }
                    }
                    const giaNum = new RegExp(/GIA Report Number[ .]+(\w+)Shape and Cutting Style/).exec(resultString);
                    const caratWeight = new RegExp(/Carat Weight[ .]+ ([0-9.]+)\s*caratColor Grade/).exec(resultString);
                    const colorGrade = new RegExp(/caratColor Grade[ .]+(\w+)\s*Clarity Grade/).exec(resultString);
                    if (giaNum !== null && giaNum[1] && giaNum[1] !== "") {
                        certGia.gia = giaNum[1];
                    }
                    if (caratWeight !== null && caratWeight[1] && caratWeight[1] !== "") {
                        certGia.carat = caratWeight[1];
                    }
                    if (colorGrade !== null && colorGrade[1] && colorGrade[1] !== "") {
                        certGia.color = colorGrade[1];
                    }
                    certGia.hash = generateUUID().toString();
                    cert.certGia = certGia;
                    this.certOwn = cert;
                    this.$store.state.nowCertOwn = this.certOwn;
                    this.$router.push({name: 'cert_sign'});
                    return;
                }).catch((e: any) => {
                    ToastUtil.show("文件不符合GIA证书格式,请重新检查文件" + e.toString());
                    this.loadingPdf = false;
                    // f.value = '';
                });
            } else {
                ToastUtil.show("文件不符合GIA证书格式,请重新检查文件");
                this.loadingPdf = false;
            }
        };
        reader.readAsDataURL(file.file);
    }

    public async readPdf(url: string) {
        const pdf = await PDFJS.getDocument(url).promise;
        const maxPages = pdf.numPages;
        const texts = []; // collecting all page promises
        for (let j = 1; j <= maxPages; j++) {
            const page = await pdf.getPage(j);
            const textContent = await page.getTextContent();
            const txt = textContent.items.map((s) => {
                return s.str;
            }).join('');
            texts.push(txt);
        }
        return texts.join('');
    }

    public onclickOtherNext(): void {
        const certOwn = new CertOwn();
        const certGia = new CertGia();
        {
            const data = [{key: this.giaKey, value: this.giaValue},
                {key: this.caratKey, value: this.caratValue},
                {key: this.colorKey, value: this.colorValue},
                {key: this.clarityKey, value: this.clarityValue},
                {key: this.cutKey, value: this.cutValue}
            ];
            const m = new Map<String, String>();
            for (const it of data) {
                m.set(it.key, it.value);
            }
            if (!this.giaValue || this.giaValue.trim() === "") {
                ToastUtil.fail("gia编号不能为空");
                return;
            } else {
                certGia.gia = this.giaValue;
            }

            if (this.caratValue && this.caratValue.trim() !== "") {
                console.log("this.caratValue===>" + this.caratValue);
                certGia.carat = this.caratValue;
            }
            if (this.colorValue && this.colorValue.trim() !== "") {
                console.log("this.colorValue===>" + this.colorValue);
                certGia.color = this.colorValue;
            }
            if (this.clarityValue && this.clarityValue.trim() !== "") {
                console.log("this.clarityValue===>" + this.clarityValue);
                certGia.clarity = this.clarityValue;
            }
            if (this.cutValue && this.cutValue.trim() !== "") {
                console.log("this.cutValue===>" + this.cutValue);
                certGia.cut = this.cutValue;
            }
        }
        certGia.hash = generateUUID().toString();
        certOwn.certGia = certGia;
        this.certOwn = certOwn;
        this.$store.state.nowCertOwn = this.certOwn;
        this.$router.push({name: 'cert_sign'});
    }
}
